.login__parent {
  background-color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.login__logo {
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
  object-fit: contain;
  width: 6.25rem;
  margin-right: auto;
  margin-left: auto;
}

.login__container {
  width: 18.75rem;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 0.313rem;
  border: 0.063rem solid lightgray;
  padding: 1.25rem;
}

.login__container > h1 {
  font-weight: 500;
  margin-bottom: 1.25rem;
}

.login__container > form > h5 {
  margin-bottom: 0.063rem;
}

.login__container > form > input {
  height: 1.875rem;
  margin-bottom: 0.625rem;
  background-color: white;
  width: 98%;
}

.login__container > p {
  margin-top: 0.938rem; /*15px */
  font-size: 12px;
}

.login__signInButton {
  background: #f0c14b;
  border-radius: 0.125rem;
  width: 100%;
  height: 30px;
  border: 0.063rem solid;
  margin-top: 0.625rem;
  border-color: #a88734 #9c7e31 #846a29;
}

.login__registerButton {
  border-radius: 2px;
  width: 100%;
  height: 1.875rem;
  border: 0.063rem solid;
  margin-top: 0.625rem;
  border-color: darkgray;
}
